@media only screen and (max-width:700px) {
    .questionnaire {
        border-collapse: collapse;
        direction: rtl;
        width: 375px;
        position: relative;
        top: 145px; }
    
    .questionnaire th, td {
        padding: 3px;
        text-align:center;
        border-bottom: 1px solid #ddd;; 
    }
    
    .questionnaire  th {
        background-color: #ADD8E6;
        color: #fff;
    }
    
    .questionnaire  tr:nth-child(even) {
        background-color: #f2f2f2;
    }

    .questionnaire .btnOk{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 24px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        position: absolute;
        width: 220px;
        height: 35px;
        left: 78px;
        background: #7EC8DF;
        top: 1107px;
    }

    .questionnaire .btnOk1{
        top: 736px;
    }

    .questionnaire .btnOk2{
        top: 1107px;
    }

    .questionnaire .gohomeQuestionnaire{
        position: absolute;
        width: 47px;
        height: 24px;
        left: 324px;
        top: -151px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;    
        display: flex;
        align-items: center;
        color: #58929C;
    }

    .questionnaire .h1Questionnaire {
        position: absolute;
        width: 256px;
        height: 39px;
        left: 0px;
        top: -135px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        line-height: 39px;    
        display: flex;
        align-items: center;
        text-align: center;
        color: #005671;
        backdrop-filter: blur(2px);  
    }

    .questionnaire .aQuestionnaire{
        position: absolute;
        font-family: 'Poppins'; 
        font-size: 18px;
    }

    .questionnaire .aQuestionnaire1{
        left: 119px;
        top: -67px;
    }

    .questionnaire .aQuestionnaire2{ 
        left: 258px;
        top: -38px;
    }

    .questionnaire .aQuestionnaire11{
        left: 12px;
        top: -67px;
    }

    .questionnaire .aQuestionnaire22{ 
        left: 12px;
        top: -38px;
    }
}    