@media only screen and (max-width:700px) {
    .question{
        background-image: url("../../img/2.jpeg");
        background-size: 103%;
        height: 100vh;
    }

    .question .realTimeAnxietyQuestionnaire
    {
        border-collapse: collapse;
        direction: rtl;
        width: 375px;
        position: relative;
        top: 268px; }
    
    .question .realTimeAnxietyQuestionnaire th, td {
        padding: 3px;
        text-align:center;
        border-bottom: 1px solid #ddd;; 
    }
    
    .question .realTimeAnxietyQuestionnaire  th {
        background-color: #ADD8E6;
        color: #fff;
    }
    
    .question .realTimeAnxietyQuestionnaire  tr:nth-child(even) {
        background-color: #f2f2f2;
    }

    .question .realTimeAnxietyQuestionnaire .btnOk{
        border: none;
        opacity: 67%;
        color: white;
        font-size: 24px;
        font-weight: bold;
        transition: all 0.5s;
        cursor: pointer;
        position: absolute;
        width: 220px;
        height: 35px;
        left: 78px;
        background: #7EC8DF;
        top: 329px;
    }

    .question .realTimeAnxietyQuestionnaire .btnOk1{
        top: 329px;
    }

    .question .realTimeAnxietyQuestionnaire .btnOk2{
        top: 347px;
    }

    .question .realTimeAnxietyQuestionnaire .gohomeQuestionnaire{
        position: absolute;
        width: 47px;
        height: 24px;
        left: 324px;
        top: -266px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;    
        display: flex;
        align-items: center;
        color: #58929C;
    }

    .question .realTimeAnxietyQuestionnaire .h1Questionnaire {
        position: absolute;
        width: 256px;
        height: 39px;
        left: 0px;
        top: -123px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        line-height: 39px;    
        display: flex;
        align-items: center;
        text-align: center;
        color: #005671;
        backdrop-filter: blur(2px);  
    }

    .question .realTimeAnxietyQuestionnaire .aQuestionnaire{
        position: absolute;
        font-family: 'Poppins'; 
        font-size: 18px;
    }

    .question .realTimeAnxietyQuestionnaire .aQuestionnaire1{
        left: 150px;
        top: -57px;
    }

    .question .realTimeAnxietyQuestionnaire .aQuestionnaire2{ 
        left: 233px;
        top: -31px;
    }

    .question .realTimeAnxietyQuestionnaire .aQuestionnaire11{
        left: 1px;
        top: -57px;
    }

    .question .realTimeAnxietyQuestionnaire .aQuestionnaire22{ 
        left: 1px;
        top: -31px;
    }
}
