@media only screen and (max-width:700px) {
  .registration-page{
      background-image: url("../../img/1.jpeg");
      background-size: 103%;
      height: 100vh;
      background-color: #eefdfc;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content:center;
  }

  .registration-page .btn{
      margin-bottom: 20%;
      width: 260px;
      height: 54px;
      background-color: #7ec8df;
      border: none;
      opacity: 67%;
      color: white;
      font-size: 24px;
      font-weight: bold;
      transition: all 0.5s;
      cursor: pointer;
    }

    .registration-page .gohome{
      position: absolute;
      width: 47px;
      height: 24px;
      left: 320px;
      top: 20px;
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;    
      display: flex;
      align-items: center;
      color: #58929C;
    }
  }    